import { CallType } from './../../../components/call-center/pbx-agent/services/models';
// export enum EDialState

export type DialStatus = 'ANSWER' | 'BUSY';
export interface IUacConfig {
    sockets: any[];
    uri: string;
    password: string;
    autoConnect: boolean;
    register: boolean;
    register_expires: number;
    session_timers: boolean;
    connection_recovery_min_interval: number;
    connection_recovery_max_interval: number;
    registrar_server: string;
    no_answer_timeout: number;
    use_preloaded_route: boolean;
    hack_via_tcp: boolean;
    hack_via_ws: boolean;
    hack_ip_in_contact: boolean;
}

export const configInitialState: IUacConfig = {
    sockets: null,
    uri: '',
    password: '',
    autoConnect: false,
    register: true,
    register_expires: 600,
    session_timers: true,
    connection_recovery_min_interval: 2,
    connection_recovery_max_interval: 30,
    registrar_server: '',
    no_answer_timeout: 60,
    use_preloaded_route: false,
    hack_via_tcp: false,
    hack_via_ws: false,
    hack_ip_in_contact: false
}


export interface IUacAuthConfig {
    sipuri: string;
    wsuri: string;
    password: string;
}

export interface IUacState {
    connected: boolean;
    connecting: boolean;
    callInProgress: boolean;
    callState: CallType,
    dialStatus: DialStatus;
    config: IUacConfig;
    wsuri: string;
    errorText?: string;
}

export const initialState: IUacState = {
    connected: false,
    connecting: false,
    callInProgress: false,
    callState: null,
    dialStatus: null,
    config: configInitialState,
    wsuri: '',
    errorText: ''
};
