import { IUacAuthConfig, IUacState } from './uac.state';
import { createFeatureSelector, createSelector, Store } from '@ngrx/store';
import { IAppState } from '..';
import { getIsConnected } from './uac.reducer';
import { Injectable } from '@angular/core';
import { distinctUntilChanged } from 'rxjs/operators';
import { Connect, ConnectSuccess, SetConfig } from './uac.actions';
import { CallspollService } from 'src/app/components/call-center/pbx-agent/services/callspoll.service';


/*const getAppState = createFeatureSelector<IAppState>('appConfig');
const getUacState = createSelector(
  getAppState,
  (state: IAppState) => state.uac
);*/
const getUacState = (state: IAppState) => state.uac;
const isConnected = createSelector(
  getUacState,
  getIsConnected
);
const config = createSelector(
  getUacState,
  (state: IUacState) => (
    {
      sipuri: state.config.uri,
      wsuri: state.wsuri,
      password: state.config.password
    } as IUacAuthConfig
  )
);

@Injectable()
export class UacStoreService {

  constructor(
    private store: Store<IAppState>,
    private callSpool: CallspollService
    ) {
      //this.callSpool.calls.subscribe(n=>console.log(n))
  }

  get isConnected$() {
    return this.store.select(isConnected).pipe(distinctUntilChanged());
  }

  get config$() {
    return this.store.select(config);
  }

  set config(c: IUacAuthConfig) {
    this.store.dispatch(new SetConfig(c));
  }

  connect() {
    //this.store.dispatch(new LogIn(p));
    this.store.dispatch(new Connect());
  }


}
