import { initialState, IUacState } from './uac.state';
import { UacActions, UacActionTypes } from './uac.actions';
import { config } from 'rxjs';

export function reducer(state = initialState, action: UacActions): IUacState {
    switch (action.type) {
        case UacActionTypes.CONNECT_START:
            return {
                ...state,
                connecting: true
            };
        case UacActionTypes.CONNECT_SUCCESS:
            return {
                ...state,
                connecting: false,
                connected: action.payload
            };
        case UacActionTypes.CONNECT_FAILURE:
            return {
                ...state,
                connecting: false,
                connected: false,
                errorText: action.payload
            };
        case UacActionTypes.SET_CONFIG:
            return {
                ...state,
                config: {
                    ...state.config,
                    uri: action.payload.sipuri, password: action.payload.password
                },
                wsuri: action.payload.wsuri
            };
        case UacActionTypes.CALL:
            return {
                ...state,
                callState: action.payload
            };
        default:
            return state;
    }
}

export { IUacState } from './uac.state';

export const getIsConnected = (state: IUacState) => state.connected;
