import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { MenuItem } from 'primeng/api';
import { environment } from '../../environments/environment';
import { IAcl, ILoginData } from '../models/login-data.model';
@Injectable({
  providedIn: 'root'
})
export class GlobalConfigService {
  private menuItems$ = new BehaviorSubject<MenuItem[]>([]);
  private loginData$ = new BehaviorSubject<ILoginData>({
    sid: '',
    auth: false,
    name: 'test',
    acl: [],
    access_token: '',
    user_id: null,
  });
  constructor() {
  }
  setLoginData(loginData) {
    this.loginData$.next(loginData);
  }
  getLoginData() {
    if (this.loginData$.getValue() === null) {
    }
    return this.loginData$.asObservable();
  }
  setMenuItems(acl) {
    this.menuItems$.next(this.aclToMenu(acl));
  }
  getMenuItems() {
    if (this.menuItems$.getValue().length === 0) {
    }
    return this.menuItems$;
  }
  private aclToMenu(acl: IAcl[]): MenuItem[] {
    const res: MenuItem[] = [];
    acl.forEach(
      (item) => {
        let it = this.getMenuItem(res, item.id);
        Object.assign(it, {
          label: item.name
        });
      }
    );
    return res;
  }
  private getMenuItem(menuItems: any[], id: string): MenuItem {
    const itemPaths = id.split('.');
    let itemsArrRef = menuItems;
    let res: MenuItem = {};
    itemPaths.forEach((path, i, paths) => {
      let itemRef;
      for (const mItem of itemsArrRef) {
        if (mItem.id === path) {
          itemRef = mItem;
          itemsArrRef = mItem.items;
          break;
        }
      }
      if (!itemRef) {
        itemRef = {
          id: path,
          items: [],
          icon: environment.menuItemsIcon[path] !== undefined ? environment.menuItemsIcon[path].icon : '',
          routerLink: ['/' + path]
        };
        itemsArrRef.push(itemRef);
        itemsArrRef = itemRef.items;
      }
      res = itemRef;
    }
    );
    return res;
  }
}
