
import { Injectable, NgZone } from '@angular/core';
import { Subject, BehaviorSubject } from 'rxjs';
import {
  IUacMessage, IUacStatus, UacEvent, UacMessageData,
  IUacConnectingData, IUacConnectedData, IUacDisconnectedData,
  IUacRegisteredData, IUacUnregisteredData, IUacRegistrationFailedData,
  IUacRegistrationExpiringData, IUacNewRTCSessionData, IUacNewMessageData
} from './models';
import { UA } from 'jssip';
import { ConfigurationService } from './configuration.service';
import { CallOptions } from './ua.service/call-options';
import { Session } from './ua.service/session';

@Injectable({ providedIn: 'root' })
export class UacService {

  public notifier: Subject<IUacMessage>;
  public status: BehaviorSubject<IUacStatus>;
  public ua: UA;
  private cacheOptions = [];

  constructor(
    private config: ConfigurationService,
    private ngZone: NgZone
  ) {
    this.notifier = new Subject<IUacMessage>();
    this.status = new BehaviorSubject<IUacStatus>(this._generateStatus('disconnected'));
  }

  connect() {
    this.ua = new UA(this.config.getConfiguration());
    this.ngZone.runOutsideAngular(() => {
      this.ua.start();
    });
    this.status.next({ connected: false, status: 'connecting' } as IUacStatus);
    this._wireStatusSubject();
    this._wireMessageSubject();
  }

  disconnect() {
    this.ua.unregister({ all: true });
    this.ua.stop();
  }

  private _wireStatusSubject(): void {
    [
      'connected',
      'disconnected',
      'registered',
      'unregistered'
    ].map(event => {
      this.ua.on(event, e =>
        // console.log(event)
        this.ngZone.run(() => this.status.next(this._generateStatus(event as UacEvent)))
      );
    });
  }

  private _wireMessageSubject(): void {

    [
      'connected',
      'connecting',
      'disconnected',
      'registered',
      'unregistered',
      'registrationFailed',
      'registrationExpiring',
      'newRTCSession',
      'newMessage'
    ].map(event => {
      this.ua.on(event, e =>
        this.ngZone.run(() =>
          this.notifier.next(this._parseEvent(event as UacEvent, e))
        )
      );
    });
  }

  private _generateStatus(status: UacEvent): IUacStatus {
    return {
      connected: (status === 'connected' || status === 'registered'),
      status
    };
  }

  private _parseEvent(name: UacEvent, event: any): IUacMessage {
    let payload: UacMessageData;

    switch (name) {
      case 'connecting': {
        payload = this._hydratePayload<IUacConnectingData>(event, ['socket', 'attemps']);
        break;
      }
      case 'connected': {
        payload = this._hydratePayload<IUacConnectedData>(event, ['socket']);
        break;
      }
      case 'disconnected': {
        payload = this._hydratePayload<IUacDisconnectedData>(event, ['socket', 'error', 'code', 'reason']);
        break;
      }
      case 'registered': {
        payload = this._hydratePayload<IUacRegisteredData>(event, ['response']);
        break;
      }
      case 'unregistered': {
        payload = this._hydratePayload<IUacUnregisteredData>(event, ['response', 'cause']);
        break;
      }
      case 'registrationFailed': {
        payload = this._hydratePayload<IUacRegistrationFailedData>(event, ['response', 'cause']);
        break;
      }
      case 'registrationExpiring': {
        payload = this._hydratePayload<IUacRegistrationExpiringData>(event, ['msg']);
        break;
      }
      case 'newRTCSession': {
        payload = this._hydratePayload<IUacNewRTCSessionData>(event, ['originator', 'request']);
        let callOptions;
        if (payload.originator === 'local') {
          callOptions = this.cacheOptions.shift();
        } else {
          callOptions = new CallOptions(this.config);
        }

        payload.session = new Session(event.session, callOptions);
        payload.session.resolveCallOptions();

        break;
      }
      case 'newMessage': {
        payload = this._hydratePayload<IUacNewMessageData>(event, ['originator', 'session', 'request']);
        break;
      }
      default: {
        throw new Error('unsuported event');
      }
    }

    return {
      event: name,
      data: payload
    } as IUacMessage;

  }

  private _hydratePayload<T>(event, fields): T {
    return fields.reduce((ret, field) => {
      if (!event) {
        return ret;
      }
      ret[field] = event[field] || null;
      return ret;
    }, {}) as T;
  }


}

