import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StoreModule } from '@ngrx/store';
import * as fromAppConfig from './';
import { EffectsModule } from '@ngrx/effects';
import { appEffects } from './';
import { UacService } from 'src/app/components/call-center/pbx-agent/services/uac.service';
import { ConfigurationService } from 'src/app/components/call-center/pbx-agent/services/configuration.service';


@NgModule({
  imports: [
    CommonModule,
    EffectsModule.forRoot(appEffects),
    StoreModule.forRoot(
      fromAppConfig.reducers,
      { metaReducers: fromAppConfig.metaReducers }
    )
  ],
  declarations: [],
  providers: [fromAppConfig.appConfigServices]
})
export class AppConfigModule { }
