import { CallspollService } from 'src/app/components/call-center/pbx-agent/services/callspoll.service';
import { UacStoreService } from './uac-store.service';
import { UacService } from './../../../components/call-center/pbx-agent/services/uac.service';
import { Injectable } from '@angular/core';
import { Effect, ofType, Actions } from '@ngrx/effects';
import { map, switchMap, tap, catchError, skip, takeWhile, takeLast, withLatestFrom, skipWhile, mergeMap, filter, first } from 'rxjs/operators';
import { UacActionTypes, ConnectFailure, ConnectSuccess, ConnectStart, Call } from './uac.actions';
import { of } from 'rxjs/internal/observable/of';
import { ConfigurationService } from 'src/app/components/call-center/pbx-agent/services/configuration.service';
import { Observable } from 'rxjs';
import { defer } from 'q';


@Injectable()
export class UacEffects {

    @Effect()
    init$ = this.uacService.status.pipe(
        skip(1),
    ).pipe(
        takeWhile((v, i) => i + 1 <= 3 && !v.connected, true),
        takeLast(1),
        map((v, i) => {
            if (v.connected) {
                return new ConnectSuccess(v.connected);
            } else {
                this.uacService.disconnect();
                return new ConnectFailure('Connect failure');
            }
        }),
    );

    @Effect({ dispatch: false })
    init1$ = this.uacService.notifier.pipe(
        map((v) => {
            //      console.log(v);
        })
    );

    @Effect()
    callsPool$ = this.callspollService.calls.pipe(
        skipWhile(n => !n.length),
        //tap(n => console.log(n)),
        //tap(n => console.log(n)),
        switchMap(calls => calls[0].status.pipe(
            map(n => new Call(n.type))
        ))
    );




    @Effect()
    connect$ = this.actions$.pipe(
        ofType(UacActionTypes.CONNECT),
        withLatestFrom(this.uacStoreService.config$),
        tap(([action, configAuth]) => {
            //console.log(storeState);
            const us = this.uacService;
            const config = this.config;
            Object.assign(config, { ...configAuth });
            us.connect();
            // return new ConnectSuccess(true);
        }),
        map(() => new ConnectStart()),
        /*switchMap(
            () =>
                this.uacService.status.pipe(
                    skip(1)
                ).pipe(
                    takeWhile((v, i) => i + 1 <= 3 && !v.connected, true),
                    takeLast(1),
                    map((v, i) => {
                        //console.log(v.status + ' - ' + i + ' - ' + v.connected);
                        //this.uacService.disconnect();
                        //return v;
                        if (v.connected) {
                            return new ConnectSuccess(v.connected);
                        } else {
                            this.uacService.disconnect();
                            return new ConnectFailure('Connect failure');
                        }
                    }),
                )
        ),*/
        catchError(error => {
            return of(new ConnectFailure(error.message || 'error'));
        })
    );

    constructor(
        private actions$: Actions,
        private uacService: UacService,
        private config: ConfigurationService,
        private uacStoreService: UacStoreService,
        private callspollService: CallspollService
    ) {
    }
}
