import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { AuthActionTypes, LogIn, LogInFailure, LogInSuccess } from './auth.actions';
import { catchError, map, switchMap, tap } from 'rxjs/operators';
import { AuthenticationService } from '../../../services/authentication.service';
import { of } from 'rxjs';
import { ILoginData, ILoginRequest } from '../../../models';
import { MessageService } from 'primeng/api';


@Injectable()
export class AuthEffects {

  @Effect()
  logIn$ = this.actions$.pipe(
    ofType(AuthActionTypes.LOGIN),

    map((action: LogIn) => action.payload),
    switchMap((payload: ILoginRequest) =>
      this.authHttpService
        .login(payload.login, payload.password)
        .pipe(
          map((userData: ILoginData) => new LogInSuccess(userData)),
          catchError(error => of(new LogInFailure(error)))
        )
    ),
  );

  @Effect({ dispatch: false })
  logInSuccess$ = this.actions$
    .pipe(
      ofType(AuthActionTypes.LOGIN_SUCCESS),
      tap(userData =>
        this.messageService
          .add({ severity: 'success', summary: 'Service Message', detail: 'Юзер залогинился' }))
    );

  @Effect({ dispatch: false })
  logInFailure$ = this.actions$
    .pipe(
      ofType(AuthActionTypes.LOGIN_FAILURE),
      tap(error => console.log(error)),
      tap((error: any) =>
        this.messageService
          .add({ severity: 'error', summary: 'Service Message', detail: error.payload.statusText }))
    );


  constructor(
    private actions$: Actions,
    private authHttpService: AuthenticationService,
    private messageService: MessageService
  ) {
  }
}
