
import { Action } from '@ngrx/store';
import { IUacAuthConfig } from './uac.state';
import { CallType } from 'src/app/components/call-center/pbx-agent/services/models';

export enum UacActionTypes {
    CONNECT = '[UAC] Connect request',
    CONNECT_START = '[UAC] Connect start',
    CONNECT_SUCCESS = '[UAC] Connect success',
    CONNECT_FAILURE = '[UAC] Connect Failure',
    SET_CONFIG = '[UAC] Set config',
    CALL = '[UAC] Call originate',
}

export class Connect implements Action {
    readonly type = UacActionTypes.CONNECT;
    //constructor(public payload: IUacAuthRequest) { }
}

export class ConnectStart implements Action {
    readonly type = UacActionTypes.CONNECT_START;
    //constructor(public payload: IUacAuthRequest) { }
}

export class ConnectSuccess implements Action {
    readonly type = UacActionTypes.CONNECT_SUCCESS;
    constructor(public payload: boolean) { }
}

export class ConnectFailure implements Action {
    readonly type = UacActionTypes.CONNECT_FAILURE;
    constructor(public payload: any) { }
}

export class SetConfig implements Action {
    readonly type = UacActionTypes.SET_CONFIG;
    constructor(public payload: IUacAuthConfig) { }
}

export class Call implements Action {
    readonly type = UacActionTypes.CALL;
    constructor(public payload: CallType) { }
}



export type UacActions =
    Connect |
    ConnectStart |
    ConnectSuccess |
    ConnectFailure |
    SetConfig |
    Call;
