import { Injectable } from '@angular/core';
import { createFeatureSelector, createSelector, Store } from '@ngrx/store';
import { distinctUntilChanged, tap } from 'rxjs/operators';
import { IAppState } from '../index';
import { getIsAuth } from './auth.reducer';
import { LogIn } from './auth.actions';

/*const getAppState = createFeatureSelector<IAppState>('appConfig');
const getAuthState = createSelector(
  getAppState,
  (state: IAppState) => {
    console.log(state.auth);
    return state.auth;
  }
);*/
const getAuthState = (state: IAppState) => state.auth;

const isAuth = createSelector(
  getAuthState,
  getIsAuth
);
@Injectable()
export class AuthService {
  constructor(private store: Store<IAppState>) {
  }
  get isAuth$() {
    return this.store.select(isAuth).pipe(distinctUntilChanged());
  }
  logIn(p) {
    this.store.dispatch(new LogIn(p));
  }
}
