import {
  ActionReducerMap,
  MetaReducer
} from '@ngrx/store';
import { environment } from '../../../environments/environment';
import * as fromAuth from './auth/auth.reducer';

import { AuthService } from './auth/auth.service';
import { AuthEffects } from './auth/auth.effects';

import * as fromUac from './uac/uac.reducer';
import { UacStoreService } from './uac/uac-store.service';
import { UacEffects } from './uac/uac.effects';

export interface IAppState {
  auth: fromAuth.IAuthState;
  uac: fromUac.IUacState;
}
export const reducers: ActionReducerMap<IAppState> = {
  auth: fromAuth.reducer,
  uac: fromUac.reducer
};
export const metaReducers: MetaReducer<IAppState>[] = !environment.production ? [] : [];
export const appConfigServices = [AuthService, UacStoreService];
export const appEffects = [AuthEffects, UacEffects];
