import { AuthActions, AuthActionTypes } from './auth.actions';
import { initialState, IAuthState } from './auth.state';

export function reducer(state = initialState, action: AuthActions): IAuthState {
  switch (action.type) {
    case AuthActionTypes.LOGIN_SUCCESS:
      return {
        ...state,
        userData: action.payload,
        isAuthenticated: action.payload.auth
      };
    case AuthActionTypes.LOGIN_FAILURE:
      return {
        ...state,
        errorText: action.payload.statusText
      };
    default:
      return state;
  }
}

export { IAuthState } from './auth.state';

export const getIsAuth = (state: IAuthState) => state.isAuthenticated;
